<template>
	<div class="msgBox" v-show='msgBox.show'>
		<div class="content">
			<div class="tit">
				<img @click='close' width="25" src="./close.png" alt="">
				{{msgBox.tit}}
			</div>
			<div class="txt">
				{{msgBox.msg}}
			</div>
		</div>
	</div>
</template>
<script>
	export default{
		props:{
			msgBox:{
				type:Object,
				default:{
					show:false,
					tit:'',
					msg:''
				}
			}
		},
		data(){
			return {

			}
		},
		methods:{
			close(){
				this.msgBox.show = false
			}
		},
		mounted(){
		}
	}
</script>
<style>
	.msgBox{
		background: rgba(0,0,0,0.5);
		position:absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		font-size: 0.16rem
	}
	.msgBox .content{
		padding:0.15rem;
		width: 80%;
		margin:0 auto;
	}
	.msgBox .content .tit{
		position: relative;
		height: 0.4rem;
		line-height: 0.4rem;
		font-size: 0.18rem
	}
	.msgBox .content .tit img{
		position: absolute;
		right: 0;
		top: 0
	}
	.msgBox .content .txt{
		text-align: left;
	}
</style>



